import React from 'react';
import style from './ProfileInfo.module.css'
import avatar from "../../../assets/images/avatar.svg";
import Preloader from "../../common/Preloader/Preloader";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";

const ProfileInfo = ({profile, status, updateStatus}) => {

    if (!profile) {
        return <Preloader />
    }

    return (
        <div>
            <div className={style.profileInfo}>
            </div>
            <div className={style.description}>
                <img src={profile.photos.large != null ? profile.photos.large : avatar} alt="avatar" />
                <ProfileStatusWithHooks status={status} updateStatus={updateStatus}/>
                <div><h2>{profile.fullName}</h2></div>
                <h3>Description:</h3>
                <div>
                    <span>About me: </span><strong>{profile.aboutMe}</strong>
                </div>
                <div>
                    <span>Looking for a job: </span><strong>{profile.lookingForAJob === true ? 'YES' : 'NO'}</strong>
                </div>
                <div>
                    {profile.lookingForAJobDescription == null ? '' :  <span>Job Status: <strong>{profile.lookingForAJobDescription}</strong></span>}
                </div>
            </div>
        </div>
    );
};

export default ProfileInfo;
